//获取初始数据
function getInitData(data){
	console.log(data);
	bankData=textChange(data.data.Bank,"bankdesc");
	sourceData=textChange(data.data.Counter,"paramname");;
	jobData=textChange(data.data.Duty,"dutyFname");
	educationData=textChange(data.data.Edu,"eduName");
	peopleData=textChange(data.data.Ethic,"paramname");
	accountData=textChange(data.data.Housetype,"paramname");
	countryData=textChange(data.data.Nation,"nationabdc");
	politicalData=textChange(data.data.Politic,"paramname");
}
/*选择器文本键的修改开始*/
function textChange(arr,key){ 
 
   for(var i=0;i<arr.length;i++){
   	    var textValue=arr[i][key];
   	    delete arr[i][key];
   	  	arr[i]["text"]=textValue;
   	  
}
  return arr;
}
/*选择器文本键的修改结束*/
