//上传附件开始
function getFileData(data){
	console.log(data);
	if(data.success){
		fileCallback(data);
	}
}
//上传附件结束
//上传附件回显开始
function fileCallback(data){
	var list=data.list;
	var eduFile=[];
	var apartFile=[];
	var otherFile1=[];
	var otherFile2=[];
	var otherFile3=[];
	//分类开始
	for(var i=0;i<list.length;i++){
		if(list[i].pictype=="010100"){
			$("#zx_bossAudit .idFront").find("img").attr("src",list[i].picpath);
		}
		if(list[i].pictype=="010101"){
			$("#zx_bossAudit .idBack").find("img").attr("src",list[i].picpath);
		}
		if(list[i].pictype.slice(0,4)=="0201"){
			eduFile[eduFile.length]=list[i].picpath;
		}
		if(list[i].pictype.slice(0,4)=="0301"){
			$(" #zx_bossAudit .bankFront").find("img").attr("src",list[i].picpath)
		}
		if(list[i].pictype.slice(0,4)=="0401"){
			apartFile[apartFile.length]=list[i].picpath;
		}
		if(list[i].pictype.slice(0,4)=="0501"){
			otherFile1[otherFile1.length]=list[i].picpath;
		}
		if(list[i].pictype.slice(0,4)=="0601"){
			otherFile2[otherFile2.length]=list[i].picpath;
		}
		if(list[i].pictype.slice(0,4)=="0701"){
			otherFile3[otherFile3.length]=list[i].picpath;
		}
	}
	//分类结束	
	
	//多张图片
	if(eduFile.length){
		for(var j=0;j<eduFile.length;j++){
			if(eduFile.length>1){
				$(".eduFiles").next().html(eduFile.length).show();
			}
			else{
				$(".eduFiles").next().hide();
			}
			var $img1=$(eduFileOne).attr("src",eduFile[j]);
			$(" #zx_bossAudit .eduFiles").append($img1);
	    }
	}
	
	if(apartFile.length){
       for(var z=0;z<apartFile.length;z++){
		 if(apartFile.length>1){
		 	
			$(".apartFiles").next().html(apartFile.length).show();
		}
		else{
			
			$(".apartFiles").next().hide();
		}
		var $img2=$(eduFileOne).attr("src",apartFile[z]);
		$(" #zx_bossAudit .apartFiles").append($img2);
	  }
	}
	if(otherFile1.length){
		for(var a=0;a<otherFile1.length;a++){
		 if(otherFile1.length>1){
			$(".otherFile1").next().html(otherFile1.length).show();
		  }
		 else{
			$(".otherFile1").next().hide();
		}
		var $img3=$(eduFileOne).attr("src",otherFile1[a]);
		$(" #zx_bossAudit .otherFile1").append($img3);
	 }
	}
    if(otherFile2.length){
      for(var b=0;b<otherFile2.length;b++){
		 if(otherFile2.length>1){
			$(".otherFile2").next().html(otherFile2.length).show();
		}
		else{
			$(".otherFile2").next().hide();
		}
		var $img4=$(eduFileOne).attr("src",otherFile2[b]);
		$(" #zx_bossAudit .otherFile2").append($img4);
	  }
    }
    if(otherFile3.length){
    	for(var c=0;c<otherFile3.length;c++){
		 if(otherFile3.length>1){
			$(".otherFile3").next().html(otherFile3.length).show();
		}
		else{
			$(".otherFile3").next().hide();
		}
		var $img5=$(eduFileOne).attr("src",otherFile2[c]);
		$(" #zx_bossAudit .otherFile3").append($img5);
	}
    }
    
}
//上传附件回显结束
function eventBind(){
     //审批轨迹开始
	 $("#zx_bossAudit .route_title li span:nth-of-type(2)").click(function(){
	 
	 	$("#zx_bossAudit .route_content").slideToggle("fast");
	 });
    //审批轨迹结束
	//团队归属开始
	$(".apartment").on("click",function(){
		if($(".referers").css("display")=="none"){
              $(".referers").slideDown();
		}
		else{
             $(".referers").slideUp();
		}
	});
	//团队归属结束
	//基本信息开始
	$(".information").on("click",function(){
	if($(".informationDetail").css("display")=="none"){
          $(".informationDetail").slideDown();
	}
	else{
         $(".informationDetail").slideUp();
	}
    });
	//基本信息结束
	//团队归属开始
	$(".upload").on("click",function(){
		if($(".uploadDetail").css("display")=="none"){
          $(".uploadDetail").slideDown();
	}
	else{
         $(".uploadDetail").slideUp();
	}
	});
	//团队归属结束
	//性别开始
		$("#zx_bossAudit .sexyGroup").click(function() {
		$(".male").removeClass("sexyActive ");
		$(".female").removeClass("sexyActive ");
		$(".unclear").removeClass("sexyActive ");
		$(this).addClass("sexyActive");
	});
	//性别结束
	//审批按钮开始
	//审批通过
	$("#okPass").find("button").click(function(){
		var option;
		var secuityNo=sessionStorage.getItem("secuityno");
		if($(".advise_input").find("textarea").val()==""){
			mui.alert("请输入审批意见");
			option=$(".advise_input").find("textarea").val();
			return;
		}
		 Clients.postClientAjax(url.zx_commonAuditPass,{secuityNo:secuityNo,option:option},function(){
		 	mui.alert("提交成功!");
		 	location.hash="#zx_approval_list";
		 });
	
	});
	//审批不通过
    $("#noPass").find("button").click(function(){
		var option;
		var secuityNo=sessionStorage.getItem("secuityno");
		if($(".advise_input").find("textarea").val()==""){
			mui.alert("请输入审批意见");
			option=$(".advise_input").find("textarea").val();
			return;
		}
		 Clients.postClientAjax(url.zx_commonAuditUnPass,{secuityNo:secuityNo,option:option},function(){
		 	mui.alert("提交成功!");
		 	location.hash="#zx_approval_list";
		 });
		 
	}); 
	//返回申请人
	$("#returnApp").find("button").click(function(){
		var option;
		var secuityNo=sessionStorage.getItem("secuityno");
		if($(".advise_input").find("textarea").val()==""){
			mui.alert("请输入审批意见");
			option=$(".advise_input").find("textarea").val();
			return;
		}
		 Clients.postClientAjax(url.zx_commonAuditReturn,{secuityNo:secuityNo,option:option},function(){
		 	mui.alert("提交成功!");
		 		location.hash="#zx_approval_list";
		 });
	});
	//审批按钮结束
	//附件上传开始
    $(" #zx_bossAudit .uploadDetail li").find("div:nth-of-type(2)").find("img").on("click",function(){
    	$("#global-shadow").html("");
        var group1=$(group);
        var u=$(this).parent().children();
        for(var o=0;o<u.length;o++){
    		var onePic=$(prePic);
    	    var onePicDiv=$(prePicDiv);
    		onePic.attr("src",u.eq(o).attr("src"));
    	    onePicDiv.find("a").append(onePic);
    		group1.find(".mui-slider-group").append(onePicDiv);
    	 }
    	
    	$("#global-shadow").html(group1);
        $("#global-shadow").show();
		var gallery = mui('.mui-slider');
		gallery.slider({
		  interval:0//自动轮播周期，若为0则不自动播放，默认为0；
		});
    });
     $(" #global-shadow").click(function(){
     	$(this).html("").hide();
     });
	//附件上传结束

}
//转换中国标准时间转化成时间格式开始
function changTime(data){
	var datetime=data.getFullYear() + '-' + (data.getMonth() + 1) + '-' + (data.getDate()-1);
	  return datetime;
}
//转换中国标准时间转化成时间格式结束	
//审批轨迹开始
function showAdvice(obj){
	if($(obj).next().css("display")=="block"){
		$(obj).next().slideUp();
		$(obj).children().eq(3).find("img").attr("src","resources/imgs/select-right-gray.jpg");
	}
	else{
		$(obj).next().slideDown();
		$(obj).children().eq(3).find("img").attr("src","resources/imgs/select-down-gray.jpg")
	}
}
//审批轨迹结束


